.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.white-content{
  background: #000000d8;
  color: white ;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    margin:0;
    padding:0;
    box-sizing:border-box;
    -webkit-user-select: none;
            user-select: none;
}

.bg-img{
    background: url(/static/media/wallpaperflare.com_wallpaper.3de0c3e4.jpg);
    height: 100vh;
    background-size: cover;
    background-position: center;
    -webkit-filter:brightness(90%);
            filter:brightness(90%);
}

.bg-img::after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.content{
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 999;
    text-align: center;
    padding: 60px 32px;
    width: 370px;
    -webkit-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);
    background: rgba(0, 0, 0, 0.3);
    /* border: 1px solid #fff; */
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    box-shadow: 0 0 6px 0 rgba(29, 29, 29, 0.203);
}

.content header{
    color:white;
    font-size: 33px;
    font-weight: 600;
    margin: 0 0 35px 0;
    font-family: 'Kdam Thmor Pro';
}

.field{
    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background:rgba(255, 255, 255, 0.133);
}

.field input{
    height:100%;
    width:100%;
    background: transparent;
    border:none;
    outline: none;
    /* color:rgb(255, 255, 255); */
    font-size: 16px;
    font-family: 'Poppins',sans-serif;
}

.space{
    margin-top: 16px;
}

.pass-key:valid ~ .show{
    display: block;
}

.field button{
    height:100%;
    width:100%;
    background: rgba(0,183,255,1);
    border: 2px solid white;
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    cursor: pointer;
    font-family: 'Kdam Thmor Pro';
    transition: .3s ease;
}

.field button:hover{
    background:rgb(0, 0, 0);
}

::-webkit-input-placeholder{
    color: white;
    opacity: 1;
}

::placeholder{
    color: white;
    opacity: 1;
}

.login{
    color:white;
    margin: 20px 0;
    font-family: 'Poppins', sans-serif;
}

i span{
    margin-left: 8px;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 16px;
    font-family: 'Poppins',sans-serif;
}
